import React,{Component} from 'react';
import {withRouter} from 'react-router-dom';
import './Search.css';
const url="https://restapp4002.herokuapp.com/city";
const rurl="https://restapp4002.herokuapp.com/rest?city=";

class Search extends Component{

    constructor(){
        super()
        this.state={
            city:'',
            rest:'',
            username:'',
            imgurl:''
        }
    }
    //displaying data in 
    renderCity=(data)=>{
        if(data){
            return data.map((item)=>{
                return  <option value={item.city}>{item.city_name}</option>
                
            })
        }

    }
    renderRest=(data)=>{
        if(data){
            return data.map((item)=>{
                return <option>{item.name}</option>
            })
        }
    }
   

    //calling API to get data
    componentDidMount(){
        fetch(url,{method:'GET'}).then((res)=>res.json()).then((data)=>this.setState({city:data}))
        const code = (this.props.location.search).split('=')[1];
        if(code){
            let requestData={
                code:code
            }
            console.log("requestData>>>",requestData)
            fetch('http://localhost:6700/users',{
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body: JSON.stringify(requestData)
            })
            .then((res)=>res.json())
            .then((data) =>{
                var user = data.login;
                var img = data.avatar_url
                sessionStorage.setItem('username',user)
                fetch(url,{method:'GET'})
                .then((res) => res.json())
                .then((data)=> this.setState({city:data,username:user,imgurl:img}))
            })
        }

    }
    handleCity=(event)=>{
        console.log(event.target.value)
        const cityId=event.target.value
        console.log(`${rurl}${cityId}`)
        fetch(`${rurl}${cityId}`,{method:'GET'}).then((res)=>res.json()).then((data)=>{this.setState({rest:data})});
    }
    handleRest=(event)=>{
        console.log(event.target.value)
        this.props.history.push(`/hoteldetails/${event.target.value}`)
        
    }
    conditionalButton =() =>{
        console.log(sessionStorage.getItem('username'))
        console.log(">>>this,state",this.state)
        if(sessionStorage.getItem('username') == null || sessionStorage.getItem('username') == undefined){
            return(
                    
                    <a href='https://github.com/login/oauth/authorize?client_id=10f25cf6b11a008eb433'>
                           <button className='login'>Login</button>
                    </a>
                    
            )
        }
        else{
            return(
                <>  
                        <img className='loginimg' src={this.state.imgurl} style={{height:50,width:50}}/>
                    
                    <span className='logintext'>
                        {this.state.username}
                    </span>
                    
                    
                </>
            )
            
        }
    }

     render(){
        return(
            <React.Fragment>
                <div class="imagecontainer img-responsive ">
              <div >
                    {this.conditionalButton()}
                     <a href="https://www.youtube.com/" target="-blank"> <img src="https://i.ibb.co/gtf1HY9/youtube.png" class="sociallogo"/></a>
                     <a href="https://www.facebook.com/" target="-blank"> <img src="https://i.ibb.co/5csJktW/facebook.png " class="sociallogo"/></a>
              </div>
              <div id="logo">
                     <b> e! </b>
              </div>

              

              <div id="heading" class="row">
                     Find Best Resturants,Cafes,Bars
              </div>

              <div class="finder">
                     <select class="locator" onChange={this.handleCity}>
                            <option>...city here...</option>
                            {this.renderCity(this.state.city)}

                     </select>

                     <select class="locator" onChange={this.handleRest}>
                         <option>...select hote...</option>
                           {this.renderRest(this.state.rest)}
                     </select>

              </div>
       </div>
               
            </React.Fragment>
        )
    }

  
}

export default withRouter(Search);